.panel {
    text-align: center;
}

.type_button {
    /* x / Base / Banner Regular */
    /* Auto Layout */

    display: flex;
    flex-direction: row;
    position: static;
    left: 3.2%;
    right: 3.2%;
    top: 10.81%;
    bottom: 5.41%;

    /* Light Background / Content Tint Background */

    background: #F5F5F5;
    /* Light / Image Border */

    border: 0.33px solid rgba(0, 0, 0, 0.08);
    box-sizing: border-box;
    border-radius: 10px;

    /* Inside Auto Layout */

    flex: none;
    order: 1;
    text-align: left;
    margin: 7px;
}

.uploader {
    background: transparent;
    border: 1px dashed var(--accent);
    height: 140px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.content {
    max-height: 148px;
    margin-top: calc(50vh - 148px);
    display: flex;
    flex-direction: column;
    position: static;
    padding-left: 12px;
    padding-right: 12px;
}

.dismiss {
    position: absolute;
    z-index: 1;
    top: 30px;
    right: 30px;
}

.wrapper {
    align-self: center;
    position: relative;
    width: 100%;
    height: 140px;
    min-width: 351px;
    overflow: hidden;

    border: 0.33px solid rgba(0, 0, 0, 0.08);
    box-sizing: border-box;
    border-radius: 10px;
}
